import React from 'react';
import ReactDOM from 'react-dom';
import { MappingLayout } from './components/DropdownComponent/MappingLayout.js';
import './index.css';
let leftOptions = [
  {
    key: '',
    text: '--select--',
    value: '--select--'
  },
  {
    key: 'Jenny Hess',
    text: 'Jenny Hess',
    value: 'Jenny Hess'
  },
  {
    key: 'Elliot Fu',
    text: 'Elliot Fu',
    value: 'Elliot Fu'
  },
  {
    key: 'Stevie Feliciano',
    text: 'Stevie Feliciano',
    value: 'Stevie Feliciano'

  },
  {
    key: 'Christian',
    text: 'Christian',
    value: 'Christian'

  },
  {
    key: 'Matt',
    text: 'Matt',
    value: 'Matt'

  },
  {
    key: 'Justen Kitsune',
    text: 'Justen Kitsune',
    value: 'Justen Kitsune'

  },
];

let rightOptions = [
  {
    key: '',
    text: '--select--',
    value: '--select--'
  },
  { key: 'af', value: 'af', text: 'Afghanistan' },
  { key: 'ax', value: 'ax', text: 'Aland Islands' },
  { key: 'al', value: 'al', text: 'Albania' },
  { key: 'dz', value: 'dz', text: 'Algeria' },
  { key: 'as', value: 'as', text: 'American Samoa' },
  { key: 'ad', value: 'ad', text: 'Andorra' },
  { key: 'ao', value: 'ao', text: 'Angola' },
  { key: 'ai', value: 'ai', text: 'Anguilla' },
  { key: 'ag', value: 'ag', text: 'Antigua' },
  { key: 'ar', value: 'ar', text: 'Argentina' },
  { key: 'am', value: 'am', text: 'Armenia' },
  { key: 'aw', value: 'aw', text: 'Aruba' },
  { key: 'au', value: 'au', text: 'Australia' },
  { key: 'at', value: 'at', text: 'Austria' },
  { key: 'az', value: 'az', text: 'Azerbaijan' },
  { key: 'bs', value: 'bs', text: 'Bahamas' },
  { key: 'bh', value: 'bh', text: 'Bahrain' },
  { key: 'bd', value: 'bd', text: 'Bangladesh' },
  { key: 'bb', value: 'bb', text: 'Barbados' },
  { key: 'by', value: 'by', text: 'Belarus' },
  { key: 'be', value: 'be', text: 'Belgium' },
  { key: 'bz', value: 'bz', text: 'Belize' },
  { key: 'bj', value: 'bj', text: 'Benin' },
]


const appendDisabled = (options) => {
  return options.map((item) => { return { ...item, disabled: false } })
}

leftOptions = appendDisabled(leftOptions);
rightOptions = appendDisabled(rightOptions);

ReactDOM.render(
  <MappingLayout leftOptions={leftOptions} rightOptions={rightOptions} />,
  document.getElementById('root')
);



